import { mockData } from './mock-data';
import axios from 'axios';
import NProgress from 'nprogress';

export const extractLocations = (events) => {
  const extractLocations = events.map((event) => event.location);
  const locations = [...new Set(extractLocations)];
  return locations;
};

export const checkToken = async (accessToken) => {
  const result = await fetch(
    `https://www.googleapis.com/oauth2/v1/tokeninfo?access_token=${accessToken}`
  )
    .then((res) => res.json())
    .catch((error) => error.json());

  return result;
};

export const getEvents = async () => {
  NProgress.start();

  if (window.location.href.startsWith('http://localhost')) {
    NProgress.done();
    return mockData;
  }

  if (!navigator.onLine) { // Check if the user is offline
    const data = localStorage.getItem('lastEvents');
    if (data !== null) {
      try {
        const parsedData = JSON.parse(data);
        if (parsedData && parsedData.events) {
          const events = parsedData.events;
          NProgress.done();
          return events;
        } else {
          throw new Error("Invalid data in localStorage: missing 'events' property.");
        }
      } catch (error) {
        console.error("Invalid data in localStorage: unable to parse JSON.", error);
        // Here you redirect user to an error page, or show some error notification.
      }
    } else {
      console.error("No data in localStorage.");
      // Here you redirect user to an error page, or show some error notification.
    }
  }

  const token = await getAccessToken();

  if (token) {
    removeQuery();
    const url = "https://ex1hgma6ae.execute-api.eu-central-1.amazonaws.com/dev/api/get-events" +
    "/" +
    token;
    const result = await axios.get(url);
    if (result.data) {
      const locations = extractLocations(result.data.events);
      localStorage.setItem('lastEvents', JSON.stringify(result.data)); // cache the fetched data in local storage
      localStorage.setItem('locations', JSON.stringify(locations));
    }
    NProgress.done();
    return result.data.events;
  }
};

export const getAccessToken = async () => {
  const accessToken = localStorage.getItem("access_token");
  const tokenCheck = accessToken && (await checkToken(accessToken));
  if (!accessToken || tokenCheck.error) {
    await localStorage.removeItem("access_token");
    const searchParams = new URLSearchParams(window.location.search);
    const code = await searchParams.get("code");
    await localStorage.setItem("code", JSON.stringify(code));
    if (!code) {
      const results = await axios.get(
        "https://ex1hgma6ae.execute-api.eu-central-1.amazonaws.com/dev/api/get-auth-url"
      );
      const { authUrl } = results.data;
      return (window.location.href = authUrl);
    }
    return code && getToken(code);
  }
  return accessToken;
};

const removeQuery = () => {
  if (window.history.pushState && window.location.pathname) {
    const newurl =
      window.location.protocol +
      '//' +
      window.location.host +
      window.location.pathname;
    window.history.pushState('', '', newurl);
  } else {
    const newurl = window.location.protocol + '//' + window.location.host;
    window.history.pushState('', '', newurl);
  }
};

const getToken = async (code) => {
  try {
    const encodeCode = encodeURIComponent(code);

    const response = await fetch(
      "https://ex1hgma6ae.execute-api.eu-central-1.amazonaws.com/dev/api/token" +
      "/" +
      encodeCode
  );
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    const { access_token } = await response.json();
    access_token && localStorage.setItem('access_token', access_token);
    return access_token;
  } catch (error) {
    error.json();
  }
};


